/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

h1 {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 49px;
  line-height: 57px;
  margin-bottom: 20px;
  color: #000000;
}

h2 {
  font-family: "Raleway", sans-serif;
  font-size: 36px;
  line-height: 42px;
  font-weight: 400;
  color: #000000;
}

h3 {
  font-family: "Raleway", sans-serif;
  font-size: 21px;
  font-weight: 700;
  line-height: 1em;
  color: #000000;
  margin-bottom: 14px;
}

h4 {
  font-family: "Raleway", sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 1em;
  color: #000000;
}

ul {
  font-family: "Open sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 41px;
  color: #000000;
  text-align: center;
}

/* header and navigation */
header {
  width: 100%;
  height: 70px;
  margin-bottom: 130px;
  background-color: #FFFFFF;
  box-shadow: #00000029 0 3px 6px;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.header-container {
  width: 83%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

div.logo {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 32px;
  color: #08A6E4;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav.navigation {
  width: 33%;
  display: flex;
  justify-content: space-around;
  align-items: stretch;
}

.nav-button {
  font-family: "Open sans", sans-serif;
  font-weight: 700;
  font-size: 11px;
  color: #A5A5A5;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-button:hover {
  color: #000000;
  border-bottom: 4px solid #08A6E4;
}

/* opening section */
section.opening {
  width: 100%;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.opening-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.plain-text {
  font-family: "Open sans", sans-serif;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 38px;
}

.button {
  font-family: "Open sans", sans-serif;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #08A6E4;
  padding: 22px 157px;
  border-radius: 40px;
  box-shadow: 5px 10px 14px #07ACE629;
}

img.background-main {
  width: 100%;
}

/* section stats */
section.stats {
  width: 100%;
  margin-bottom: 121px;
  display: flex;
  justify-content: center;
  align-items: center;
}

div.stats-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h2.stats {
  margin-bottom: 100px;
}

div.stats-icon-container {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

div.stats-icon-column {
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.stats-icon {
  margin-bottom: 14px;
}

.stats-icon-background {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 30px;
}

.stats-icon-image[alt*=person] {
  position: absolute;
  top: 5px;
  left: 27px;
}

.stats-icon-image[alt*=eye] {
  position: absolute;
  top: 11px;
  left: 17px;
}

.stats-icon-image[alt*=catalog] {
  position: absolute;
  top: 5px;
  left: 26px;
}

.stats-icon-background-red {
  background-color: #FB3B64;
}

.stats-icon-background-blue {
  background-color: #08A6E4;
}

.stats-icon-background-green {
  background-color: #55DFB4;
}

p.number {
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: 800;
  line-height: 24px;
  letter-spacing: 6px;
  margin-bottom: 19px;
}

p.number-description {
  font-family: "Raleway", sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: 17px;
  color: #A5A5A5;
  text-align: center;
}

/* section benefits */
section.benefits {
  width: 100%;
  padding-top: 106px;
  padding-bottom: 70px;
  margin-bottom: 100px;
  background-color: #F7F7F7;
  display: flex;
  justify-content: center;
  align-items: center;
}

.benefits-container {
  width: 70%;
}

.benefit-container-primary {
  width: 100%;
  background-color: #FFFFFF;
  margin-bottom: 37px;
  padding: 91px 35px;
  border: 1px solid #E8E9ED;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.benefit-container-secondary {
  width: 100%;
  background-color: #F7F7F7;
  margin-bottom: 37px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

div.benefit-description-container {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.benefit-container-secondary div.benefit-description-container {
  width: 55%;
  padding: 91px 35px;
  background-color: #FFFFFF;
  border: 1px solid #E8E9ED;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.benefit-description-container h2 {
  margin-bottom: 30px;
}

.benefit-description {
  font-family: "Open sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.benefit-description-underline {
  width: 70%;
  height: 4px;
}

.benefit1-description-underline {
  background-color: #FB3B64;
}

.benefit2-description-underline {
  background-color: #08A6E4;
}

.benefit3-description-underline {
  background-color: #55DFB4;
}

div.benefit-img {
  position: relative;
  width: 37%;
}

.benefit-container-secondary div.benefit-img {
  background-color: #55DFB4;
  border: 1px solid #E8E9ED;
  display: flex;
  justify-content: center;
  align-items: center;
}

.benefit1-img-bg {
  position: absolute;
  top: -104px;
  left: 100px;
  width: 12vw;
  height: 12vw;
  border-radius: 31px;
  background-color: #FB3B64;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
}

.benefit2-img-bg {
  position: absolute;
  top: -163px;
  left: 33px;
  width: 25vw;
  height: 25vw;
  border-radius: 50vw;
  background-color: #08A6E4;
}

img.benefit1-img-photo {
  position: absolute;
  top: -54px;
  left: -53px;
  width: 100%;
}

img.benefit2-img-photo {
  position: absolute;
  top: -103px;
  left: 75px;
  width: 120%;
}

img.benefit3-img-photo {
  width: 70%;
}

/* section pricing */
section.pricing {
  width: 100%;
  margin-bottom: 180px;
  display: flex;
  justify-content: center;
}

h2.pricing {
  margin-bottom: 50px;
}

p.price {
  font-family: "Open sans", sans-serif;
  font-size: 46px;
  font-weight: 300;
  line-height: 64px;
}

.pricing-plan-basic p.price {
  color: #FB3B64;
}

.pricing-plan-professional p.price {
  color: #08A6E4;
}

.pricing-plan-premium p.price {
  color: #55DFB4;
}

div.pricing-container {
  width: 78%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.plans-container {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 59px;
}

.pricing-plan-container {
  position: relative;
  width: 30%;
  padding: 34px 0 80px 0;
  border: 1px solid;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.button-secondary {
  content: "Begin";
  font-size: 13px;
  text-align: center;
  padding: 16px 57px;
  border-radius: 30px;
  position: absolute;
  bottom: -25px;
}

.pricing-plan-basic {
  height: 92%;
  border-color: #FB3B64;
}

.pricing-plan-basic .button {
  background-color: #FB3B64;
  box-shadow: none;
}

.pricing-plan-professional {
  height: 100%;
  width: 32%;
  border-color: #08A6E4;
  box-shadow: 5px 10px 27px #07ACE629;
}

.pricing-plan-professional .button {
  background-color: #08A6E4;
  padding: 16px 64px;
}

.pricing-plan-premium {
  height: 92%;
  border-color: #55DFB4;
}

.pricing-plan-premium .button {
  background-color: #55DFB4;
  box-shadow: none;
}

.pricing ul {
  margin-top: auto;
}

.pricing li {
  margin-top: auto;
  line-height: 41px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

li.included::before {
  content: "";
  width: 11px;
  height: 11px;
  background-image: url("../assets/Ckechmark-1.svg");
}

li.not-included::before {
  content: "";
  width: 11px;
  height: 11px;
  background-image: url("../assets/Close.svg");
}

/* section contact */
section.contact {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 59px;
}

.contact-container {
  width: 55%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.info-container {
  width: 50%;
}

.contact-form {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.info {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 20px;
}

.info p {
  font-family: "Open sans", sans-serif;
  font-weight: 600;
  color: #A5A5A5;
  line-height: 22px;
  display: flex;
  gap: 10px;
}

.info p:before {
  content: "";
  height: 1.5em;
  width: 1.5em;
  background-image: url("../assets/Mail Icon.svg");
  background-repeat: no-repeat;
  background-size: cover;
  align-self: center;
}

.info p:first-child::before {
  background-image: url("../assets/Mail Icon.svg");
}

.info p:last-child::before {
  background-image: url("../assets/Phone Icon.svg");
}

form .button {
  position: relative;
  align-self: flex-end;
  width: 50%;
}

label {
  font-family: "Open sans", sans-serif;
  font-size: 11px;
  font-weight: 700;
  line-height: 15px;
  text-transform: uppercase;
  color: #08A6E4;
}

.contact-form input {
  -webkit-appearance: none;
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #A5A5A5;
  margin-bottom: 30px;
}

.checkbox-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

input[type=checkbox] {
  width: 24px;
  height: 24px;
  border: 1px solid #A5A5A5;
}

input[type=checkbox]:checked {
  background-image: url("../assets/Checkmark.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.consent {
  width: 100%;
  padding-left: 10px;
  font-family: "Open sans", sans-serif;
  font-size: 12px;
  color: #A5A5A5;
}

/* footer */
footer {
  width: 100%;
  height: 90px;
  background-color: #F7F7F7;
  box-shadow: 0px -5px 40px #00000029;
  padding: 0 137px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer .logo {
  justify-content: flex-start;
}

.copyright {
  font-family: "Open sans", sans-serif;
  font-size: 11px;
  line-height: 15px;
  color: #A5A5A5;
  margin-top: 6px;
}

.social {
  width: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-twitter {
  width: 24px;
  height: 24px;
  background-image: url("../assets/Twitter.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.logo-facebook {
  width: 24px;
  height: 24px;
  background-image: url("../assets/Facebook.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}